import { useTheme } from "@mui/material";
import React from "react";

export const ConstructionFold = () => {
  const theme = useTheme();

  return (
    <div style={{ position: "absolute", top: 0, right: 0 }}>
      <div
        style={{
          width: 0,
          height: 0,
          borderLeft: "64px solid transparent",
          borderTop: "64px solid " + theme.palette.primary.main,
        }}
      ></div>
      <div style={{ position: "absolute", top: 0, right: 0, fontSize: 24 }}>🚧</div>
    </div>
  );
};
