import { Typography } from "@mui/material";
import React from "react";
import {
  CChart,
  createIRSI,
  defaultDarkTheme,
  defaultLightTheme,
  iEMA,
  iKAMA,
  Types as T,
  useChartState,
} from "react-tech-chart";
import { iBuyStopper } from "../Site/BuyStopper";
import { fbExampleData } from "../Site/fbDataset";
import { iSellStopper } from "../Site/SellStopper";

export type HomeProps = {
  darkMode: boolean;
};

const settings: T.ChartStateProps["settings"] = {
  disablePointerEvents: false,
  disableTheme: true,
  initialState: defaultDarkTheme,
  initWidthPerTick: 8,
  // maxUpdatesPerSec: 40
};

export const Home = (props: HomeProps) => {
  const { darkMode } = props;
  const [Data, setData] = React.useState<any>(fbExampleData.map((dat: any) => ({ ...dat, date: new Date(dat.date) })));
  const Controller = useChartState({
    data: [
      { data: Data, name: "Meta Platforms", type: "chart", id: "mainchart" },
      {
        id: "kama_01",
        type: "indicator",
        indicator: iKAMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },

      { type: "indicator", indicator: createIRSI({ period: 5 }), id: "rsi_01" },
      {
        id: "ema_rsi_01",
        type: "indicator",
        indSrcId: "rsi_01",
        indicator: iEMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },
    ],
    rtData: undefined,
    settings,
  });
  const { ChartState, Dispatch } = Controller;

  React.useEffect(() => {
    if (darkMode !== ChartState.options.isDarkMode) {
      Dispatch({ task: "setTheme", params: { theme: darkMode ? defaultDarkTheme : defaultLightTheme } });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [darkMode]); // force theme updates only if prop darkMode changes (triggered by Menu in Layout)

  return (
    <React.Fragment>
      <div
        style={{
          textAlign: "center",
          // background: theme.palette.background.paper,
        }}
      >
        <img src="./logo128.png" alt="logo" width={128} height={128}></img>
      </div>
      <div
        style={{
          textAlign: "center",
          paddingBottom: "8px",
        }}
      >
        <Typography color="text.primary">
          an interactive and extensible react charting tool designed for technical chart analysis.
        </Typography>
        <Typography color="secondary.main">
          experimental preview - <u>unreleased</u> yet
        </Typography>
      </div>
      <div style={{ flexGrow: 1 }}>
        <div style={{ height: "100%" }}>
          <CChart Controller={Controller} />
        </div>
      </div>
    </React.Fragment>
  );
};
