import { mdiThemeLightDark, mdiGithub, mdiMenu, mdiReact, mdiHook, mdiTools } from "@mdi/js";
import {
  Toolbar,
  Typography,
  Button,
  IconButton,
  useTheme,
  Box,
  Stack,
  useMediaQuery,
  Menu,
  MenuProps,
  Divider,
  MenuItem,
  TypographyProps,
  Popover,
  Container,
  Link as ExtLink,
} from "@mui/material";
import { Icon } from "@mdi/react";
import React from "react";
import { styled } from "@mui/material";

import { alpha } from "@mui/material/styles";
import { TreeItem, treeItemClasses, TreeItemProps } from "@mui/lab";
import { Link, useNavigate} from "react-router-dom";
import { licenses } from "../Site/depLics";
import { NavMenu } from "./NavMenu";

export const MenuIconButton = styled(IconButton)(({ theme }) => ({
  color: theme.palette.primary.main,
}));

export const StyledTreeItems = styled(
  (
    props: TreeItemProps & {
      labelText?: string;
      TypographyProps?: TypographyProps;
      infoNode?: React.ReactNode;
      to?: string;
      iconNode?: React.ReactNode;
      onClick?: () => void;
    }
  ) => {
    const { labelText, TypographyProps, infoNode, iconNode, to, label, onClick, ...treeItemProps } = props;
    const labelTypo = (
      <Typography variant="h6" component="span" {...TypographyProps}>
        {labelText}
      </Typography>
    );
    return (
      <TreeItem
        onClick={onClick}
        label={
          !label ? (
            <div style={{ display: "flex", alignItems: "center", gap: 8, alignContent: "center" }}>
              {iconNode}
              {to ? <Link to={to}>{labelTypo}</Link> : labelTypo}
              {infoNode}
            </div>
          ) : (
            label
          )
        }
        {...treeItemProps}
      ></TreeItem>
    );
  }
)(({ theme }) => ({ [`& .${treeItemClasses.content}`]: { paddingTop: 4, paddingRight: 4, paddingBottom: 4 } }));

const StyledMenu = styled((props: MenuProps) => (
  <Menu
    elevation={5}
    anchorOrigin={{
      vertical: "bottom",
      horizontal: "right",
    }}
    transformOrigin={{
      vertical: "top",
      horizontal: "left",
    }}
    {...props}
  />
))(({ theme }) => ({
  "& .MuiPaper-root": {
    borderRadius: 12,
    marginTop: 8,
    paddingRight: 0,
    minWidth: 180,

    color: theme.palette.mode === "light" ? "rgb(55, 65, 81)" : theme.palette.grey[300],
    "& .MuiMenuItem-root": {
      "&:active": {
        backgroundColor: alpha(theme.palette.primary.main, theme.palette.action.selectedOpacity),
      },
    },
  },
}));

export type LayoutProps = { setDarkMode: (current: any) => any };
export const Layout: React.FC<LayoutProps> = (props) => {
  const { setDarkMode } = props;
  const theme = useTheme();
  const navigate = useNavigate();
  const isBiggerSm = useMediaQuery(theme.breakpoints.up("sm"));
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const Anchor = React.useRef<HTMLElement | null>(null);
  const [BottomPopover, setBottomPopover] = React.useState<
    { type: "license"; anchor: any } | { type: "deps"; anchor: any } | null
  >(null);

  const [MenuOpen, setMenuOpen] = React.useState(false);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  // const handleMenuOpen = React.useCallback(() => {
  //   setMenuOpen(true);
  // }, []);
  const handleMenuToggle = React.useCallback(() => {
    setMenuOpen((current) => !current);
  }, []);

  return (
    <React.Fragment>
      <Stack id="layoutRoot" sx={{ height: "100%" }}>
        <Box
          ref={Anchor}
          sx={{
            bgcolor: theme.palette.background.paper,
            zIndex: 1000,
            boxShadow: theme.shadows[3],
            pb: 0,
            display: "relative",
            top: 0,
          }}
        >
          <Toolbar sx={{ pr: 1, alignItems: "center", alignContent: "center" }}>
            <Link to="/">
              <img
                src={"./logo64.png"}
                alt="logo"
                width={isBiggerSm ? 50 : 40}
                height={isBiggerSm ? 50 : 40}
                // layout="fixed"
              ></img>
            </Link>

            <Box sx={{ flexGrow: 1, pl: 1 }}>
              <Typography
                variant="h3"
                color="primary.main"
                sx={{ "& :hover": { textDecoration: "none" } }}
                component="div"
              >
                <Link to="/">{isBiggerSm ? "react-tech-chart" : "rtc"}</Link>
              </Typography>
            </Box>
            <Button id="docs-nav-button" onClick={handleClick} style={{ textTransform: "none" }}>
              <Typography variant="h5" component="div" color="primary.main">
                Docs
              </Typography>
            </Button>
            <StyledMenu
              id="docs-nav-menu"
              MenuListProps={{
                "aria-labelledby": "docs-nav-button",
              }}
              anchorEl={anchorEl}
              open={!!anchorEl}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose} disableRipple>
                <Icon path={mdiHook} size={1} color={theme.palette.primary.main} style={{ marginRight: 8 }} />
                useChartState 🚧❌
              </MenuItem>
              <MenuItem onClick={handleClose} disableRipple>
                <Icon path={mdiReact} size={1} color={theme.palette.primary.main} style={{ marginRight: 8 }} />
                Chart component 🚧❌
              </MenuItem>
              <MenuItem onClick={handleClose} disableRipple>
                <Icon path={mdiTools} size={1} color={theme.palette.primary.main} style={{ marginRight: 8 }} />
                Utilities 🚧❌
              </MenuItem>
            </StyledMenu>
            <MenuIconButton
              size="small"
              //   sx={{ color: theme.palette.primary.main }}
              aria-label="menu"
              onClick={() => setDarkMode((current: any) => !current)}
            >
              <Icon path={mdiThemeLightDark} size={1.2} />
            </MenuIconButton>
            <MenuIconButton
              size="small"
              aria-label="menu"
              onClick={() => {
                window?.open?.("https://github.com/carmnk/react-techchart", "_blank", "noopener");
              }}
            >
              <Icon path={mdiGithub} size={1.2} />
            </MenuIconButton>
            <MenuIconButton size="small" aria-label="menu" onClick={handleMenuToggle}>
              <Icon path={mdiMenu} size={1.2} />
            </MenuIconButton>
          </Toolbar>
        </Box>

        <div
          style={{
            background: theme.palette.background.paper,
            position: "relative",
            top: 0,
            height: "100%",
            overflowY: "auto",
            paddingTop: 0,
            display: "flex",
            flexDirection: "column",
          }}
        >
          <div style={{ position: "relative", top: 0, flexGrow: 1 }}>{props.children}</div>
          <Divider />
          <div
            style={{
              height: "37px",
              background: theme.palette.background.paper,
              color: theme.palette.primary.main,
              padding: "4px 24px",
              textAlign: "center",
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyItems: "center",
              justifyContent: "center",
              gap: 4,
            }}
          >
            <Typography variant={"hText" as any} style={{ fontSize: "16px" }}>
              <a href="https://github.com/carmnk" target="_blank" rel="noopener noreferrer">
                {`© C. Menk`}
              </a>
            </Typography>
            <Typography variant={"hText" as any} style={{ fontSize: "16px" }}>
              {`-`}
            </Typography>
            <Typography
              variant={"hText" as any}
              color="primary.main"
              sx={{
                fontSize: "16px",
                background: "transparent",
                border: "none",
                p: 0,
                "&:hover": { textDecoration: "underline" },
              }}
              component="button"
              onClick={(e: React.MouseEvent<HTMLButtonElement>) => {
                setBottomPopover({ type: "license", anchor: e.target });
              }}
            >
              license: MIT
            </Typography>
            <Popover
              open={!!BottomPopover && BottomPopover?.type === "license"}
              onClose={() => {
                setBottomPopover(null);
              }}
              anchorEl={!!BottomPopover ? BottomPopover.anchor : undefined}
              anchorOrigin={{
                vertical: "top",
                horizontal: "center",
              }}
              transformOrigin={{
                vertical: "bottom",
                horizontal: "center",
              }}
            >
              <Container sx={{ p: 2 }} maxWidth="md">
                <Typography variant={"h6" as any} color="primary.main" component="div" sx={{ pb: 1 }}>
                  MIT License (react-tech-chart library)
                </Typography>
                <Divider />
                <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="div">
                  Copyright (c) 2021 C. Menk
                </Typography>
                <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="div">
                  Permission is hereby granted, free of charge, to any person obtaining a copy of this software and
                  associated documentation files (the "Software"), to deal in the Software without restriction,
                  including without limitation the rights to use, copy, modify, merge, publish, distribute, sublicense,
                  and/or sell copies of the Software, and to permit persons to whom the Software is furnished to do so,
                  subject to the following conditions:
                </Typography>
                <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="div">
                  The above copyright notice and this permission notice shall be included in all copies or substantial
                  portions of the Software.
                </Typography>
                <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="div">
                  THE SOFTWARE IS PROVIDED "AS IS", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR IMPLIED, INCLUDING BUT NOT
                  LIMITED TO THE WARRANTIES OF MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. IN
                  NO EVENT SHALL THE AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER LIABILITY,
                  WHETHER IN AN ACTION OF CONTRACT, TORT OR OTHERWISE, ARISING FROM, OUT OF OR IN CONNECTION WITH THE
                  SOFTWARE OR THE USE OR OTHER DEALINGS IN THE SOFTWARE.
                </Typography>
                <Divider />
                <Typography variant={"para1" as any} sx={{ fontSize: 16, pt: 2 }} component="div">
                  Remark (not part of the license text):{" "}
                </Typography>
                <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="span">
                  This library is using additional dependencies. When using the library, please don't forget to
                  attribute the creators of these dependencies.{" "}
                </Typography>
                <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="span">
                  <Link
                    to="legal"
                    onClick={() => {
                      setBottomPopover(null);
                    }}
                  >
                    See licenses and attribution
                  </Link>
                </Typography>
              </Container>
            </Popover>
            <Typography variant={"hText" as any} style={{ fontSize: "16px" }}>
              {`-`}
            </Typography>
            <Typography
              variant={"hText" as any}
              color="primary.main"
              sx={{
                fontSize: "16px",
                background: "transparent",
                border: "none",
                p: 0,
                "&:hover": { textDecoration: "underline" },
              }}
              component="button"
              onClick={(e: React.MouseEvent<HTMLButtonElement>) => {
                // setBottomPopover({ type: "deps", anchor: e.target });
              }}
            >
              <Link to="legal">licenses and attribution</Link>
            </Typography>
          </div>
        </div>

        <NavMenu open={MenuOpen} onToggle={handleMenuToggle} onNavigate={navigate} />
      </Stack>
    </React.Fragment>
  );
};
