import { mdiChevronRight, mdiChevronDown, mdiCheckCircle } from "@mdi/js";
import Icon from "@mdi/react";
import { TreeView } from "@mui/lab";
import { Container, Theme, Typography, TypographyProps } from "@mui/material";
import { features } from "process";
import React from "react";
import { StyledTreeItems } from "../layout/Layout";

export const Features = (props: { theme: Theme }) => {
  const { theme } = props;

  //   const theme = DarkMode ? muiDarkSiteTheme : muiLightSiteTheme;

  const features: {
    cat: "chart" | "indicators" | "tools" | "interactions";
    nodeId: string;
    labelText?: string;
    label?: React.ReactNode;
    TypographyProps: TypographyProps;
    status: "good" | "ok" | "nok";
  }[] = [
    {
      cat: "chart",
      nodeId: "features-containerMode",
      labelText: "responsive or static container",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "chart",
      nodeId: "features-styles",
      labelText: "customizable chart styles and theming",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "chart",
      nodeId: "features-themingToggle",
      labelText: "chart styles and theming can be modified by user",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "chart",
      nodeId: "features-charttypes",
      labelText: "supports currently candle-, line- and barchart",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },

    {
      cat: "chart",
      nodeId: "features-mainchart",
      labelText: "supports currently only one dataseries (no benchmarks, but indicators)",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "ok",
    },
    {
      cat: "chart",
      nodeId: "features-fullscreen",
      label: (
        <React.Fragment>
          <div style={{ display: "flex", alignItems: "center", gap: 8, alignContent: "center" }}>
            <div style={{ width: 24, height: 24 }}>
              <Icon path={mdiCheckCircle} size={"24px"} color={theme.palette.primary.main} />
            </div>
            <Typography variant="body1" color="text.primary" component="span">
              supports fullscreen mode via{" "}
              <a href="https://developer.mozilla.org/en-US/docs/Web/API/Fullscreen_API">
                <u>js api</u>
              </a>
              , good for anyone except safari ios/ie.
            </Typography>
          </div>
        </React.Fragment>
      ),

      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "chart",
      nodeId: "features-multipleSubs",
      labelText: "supports multiple subcharts (currently 1 yaxis per subchart)",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "ok",
    },
    {
      cat: "chart",
      nodeId: "features-fetching",
      labelText: "can fetch and display csv-files locally or via url",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "interactions",
      nodeId: "drag-interactions",
      labelText: "supports drag events (scale on xaxis, translate on chart area)",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "interactions",
      nodeId: "wheel-interactions",
      labelText: "supports mouse wheel event (scale)",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "interactions",
      nodeId: "pinch-interactions",
      labelText: "supports pinch events (scale)",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "indicators",
      nodeId: "features-indMultiple",
      labelText: "comes with multiple common indicators like SMA, EMA, RSI, ATR etc..",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "indicators",
      nodeId: "features-extensible",
      labelText: "additional indicators can be implemented.",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "indicators",
      nodeId: "features-chaining",
      labelText: "indicators can be based on dataseries or other indicators (like SMA of RSI)",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
    {
      cat: "tools",
      nodeId: "features-tools",
      labelText: "supports customizable chart drawings or tools - currently only vertical-, horizontal- and trendline",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "ok",
    },
    {
      cat: "tools",
      nodeId: "features-toolsSnap",
      labelText: "supports snapping of tool anchor points to graphs. e.g. snap chart high with an horziontal line",
      TypographyProps: { variant: "body1", color: "text.primary" },
      status: "good",
    },
  ];

  return (
    <Container
      maxWidth="lg"
      sx={{ pt: 2, minHeight: "100%", position: "relative", top: 0, display: "flex", flexDirection: "column" }}
    >
      <Typography color="primary.main" component="div" variant="h3">
        Features
      </Typography>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        {`The chart component takes by default a high-level approach.
                This means you get a chart component almost at app-level but this convenience is paid with loss of flexibility.
                Mayor design/architecture decisions have been made and may not be easy to opt out.
                For example Dom-Elements are using the "@mui/material" and "@mui/lab" library while Canvas-Elements are based on the "konva" and "react-konva" library. 
              `}
      </Typography>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        However if possible the api does (or will) provide ways to opt-out of several feature. The ChartState hook does
        only calculate the necessary values to display the chart. It can generally be consumed by any drawing/canvas
        element or library. The chart component is composed of canvas layers, chart labels, chart menu incl.
        open-button. All of these subcomponents except the canvas layers will be replaceable.
      </Typography>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        Current Features
      </Typography>

      <div>
        <TreeView
          // sx={{ height: "max-content" }}
          // defaultParentIcon={}
          defaultExpanded={["treeview-chart", "treeview-indicators", "treeview-tools", "treeview-interactions"]}
          defaultChecked={true}
          defaultExpandIcon={<Icon path={mdiChevronRight} size={1} color={theme.palette.primary.main} />}
          defaultCollapseIcon={<Icon path={mdiChevronDown} size={1} color={theme.palette.primary.main} />}
        >
          {["chart", "interactions", "indicators", "tools"].map((cat, cIdx) => (
            <StyledTreeItems
              key={"treeview-" + cat}
              nodeId={"treeview-" + cat}
              labelText={cat}
              TypographyProps={{ color: "primary", variant: "h5" }}
            >
              {features
                .filter((f) => f.cat === cat)
                .map((feature, fIdx) => (
                  <StyledTreeItems
                    key={feature.nodeId}
                    nodeId={feature.nodeId}
                    label={feature.label}
                    labelText={feature.labelText}
                    TypographyProps={feature.TypographyProps}
                    iconNode={
                      <div style={{ width: 24, height: 24 }}>
                        <Icon
                          path={mdiCheckCircle}
                          size={"24px"}
                          color={
                            feature.status === "good"
                              ? theme.palette.primary.main
                              : feature.status === "ok"
                              ? theme.palette.warning.main
                              : theme.palette.secondary.main
                          }
                        />
                      </div>
                    }
                  ></StyledTreeItems>
                ))}
            </StyledTreeItems>
          ))}
        </TreeView>
      </div>
    </Container>
  );
};
