import React from "react";
import { Box, Container, IconButton, Chip, Typography, useTheme } from "@mui/material";
import {
  CChart,
  Types as T,
  defaultDarkTheme,
  useChartState,
  iKAMA,
  iEMA,
  createIRSI,
  defaultLightTheme,
} from "react-tech-chart";
import Icon from "@mdi/react";
import { mdiArrowAll, mdiArrowExpandAll } from "@mdi/js";
import { fbExampleData } from "../../Site/fbDataset";
import { useDrag } from "@use-gesture/react";

const staticSettings: T.ChartStateProps["settings"] = {
  // disablePointerEvents: true,
  disableTheme: true,
  initialState: defaultDarkTheme,
  initWidthPerTick: 8,
  containerMode: "static",
  // maxUpdatesPerSec: 40
  // additionalIndicators: [iBuyStop, iSellStop],
};

const appendInteractiveUiElements = (resizeBind: any, translateBind: any) => {
  return {
    appendElement: (
      <React.Fragment>
        <Box
          {...(resizeBind as any)?.()}
          sx={{
            // touchAction: "none",
            zIndex: 2200,
            position: "absolute",
            bottom: 0,
            right: 0,
            touchAction: "none",
            userSelect: "none",
          }}
        >
          <IconButton
            sx={{
              bgcolor: "secondary.main",
              "&:hover": { bgcolor: "secondary.dark" },
            }}
            disableFocusRipple
            disableTouchRipple
          >
            <Icon path={mdiArrowExpandAll} size={1} color="#fff"></Icon>
          </IconButton>
        </Box>
        <Box
          id="halo213"
          {...(translateBind as any)?.()}
          sx={{
            zIndex: 2200,
            position: "absolute",
            bottom: "50%",
            right: "50%",
            touchAction: "none",
            userSelect: "none",
          }}
        >
          <IconButton
            sx={{
              bgcolor: "secondary.main",
              "&:hover": { bgcolor: "secondary.dark" },
            }}
            disableFocusRipple
            disableTouchRipple
          >
            <Icon path={mdiArrowAll} size={1} color="#fff"></Icon>
          </IconButton>
        </Box>
      </React.Fragment>
    ),
  };
};

export type ExampleBasicsProps = {
  darkMode: boolean;
};

export const ExamplesInteractiveContainer = (props: ExampleBasicsProps) => {
  const { darkMode } = props;
  const theme = useTheme();

  const [Size, setSize] = React.useState<{ init: [number, number] | null; current: [number, number] | null } | null>(
    null
  );
  const [Translation, setTranslation] = React.useState({ xy: [0, 0], active: false });

  const resizeBind = useDrag((dragState) => {
    if (!Size?.current) return;
    setSize((current) => {
      if (!current?.current) return current;
      // console.log(
      //   Size.current,
      //   dragState?.movement,
      //   dragState.active,
      //   dragState.active && !current.init ? current?.current : !dragState.active ? null : current.init
      // );
      return {
        init: dragState.active && !current.init ? current?.current : !dragState.active ? null : current.init,
        current: [
          current.init ? current.init[0] + dragState.movement[0] : current.current[0],
          current.init ? current.init[1] + dragState.movement[1] : current.current[1],
        ],
      };
    });
  });

  const translateBind = useDrag((dragState) => {
    console.log("TRANSLATION", Translation);
    if (!Translation) return;
    setTranslation((current) => ({
      xy: [current.xy[0] + dragState.delta[0], current.xy[1] + dragState.delta[1]],
      active: dragState.active,
    }));
  });

  const [Data, setData] = React.useState<any>(fbExampleData.map((dat) => ({ ...dat, date: new Date(dat.date) })));
  const Controller = useChartState({
    width: Size?.current?.[0] || 0,
    height: Size?.current?.[1] || 400,
    data: [
      { data: Data, name: "Meta Platforms", type: "chart", id: "mainchart" },
      {
        id: "kama_01",
        type: "indicator",
        indicator: iKAMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },

      { type: "indicator", indicator: createIRSI({ period: 5 }), id: "rsi_01" },
      {
        id: "ema_rsi_01",
        type: "indicator",
        indSrcId: "rsi_01",
        indicator: iEMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },
    ],
    rtData: undefined,
    settings: {
      ...staticSettings,
      disablePointerEvents: Translation?.active || !!Size?.init,
      ...appendInteractiveUiElements(resizeBind, translateBind),
    },
  });
  const { ChartState, Dispatch, ContainerRef } = Controller;

  const parentWidth = ContainerRef?.current?.parentElement?.offsetWidth;
  const parentHeight = ContainerRef?.current?.parentElement?.clientHeight;
  if (!Size && parentWidth && parentHeight) setSize({ current: [parentWidth, parentHeight], init: null });

  React.useEffect(() => {
    if (darkMode !== ChartState.options.isDarkMode) {
      Dispatch({ task: "setTheme", params: { theme: darkMode ? defaultDarkTheme : defaultLightTheme } });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [darkMode]); // force theme updates only if prop darkMode changes (triggered by Menu in Layout)

  return (
    <Container
      maxWidth="lg"
      sx={{ pt: 2, minHeight: "100%", position: "relative", top: 0, display: "flex", flexDirection: "column" }}
    >
      <Typography color="primary.main" component="div" variant="h3">
        Interactive Container
      </Typography>

      <div style={{ paddingTop: 8, paddingBottom: 8 }}>
        <Typography color="text.primary" component="span">
          By setting
        </Typography>
        <Chip label="containerMode" size="small" />
        <Typography color="text.primary" component="span">
          to static <u>and</u>
          {` providing width and height propertys you can control the chart's dom container
                size. Static containerMode is suitable for cases in which the chart size is either explicitely known`}{" "}
          <i>in pixels </i>
          or to be controlled from outside the chart component.
        </Typography>
      </div>
      <div style={{ paddingTop: 8, paddingBottom: 8 }}>
        <Typography color="text.primary" component="div" style={{ paddingBottom: 8 }}>
          This examples demonstrates the latter case by adding a translate and resize logic for the
          <i> container</i> on top of the chart component.
          <br />
          The containerSize (width, height) is controlled by an additional state outside the chart component. In order
          to translate the chart container within the viewport the chart component is wrapped in a {`<div/>`} element
          with css transform.
        </Typography>
      </div>
      <div
        style={{
          transform: `translate(${Translation?.xy?.[0] ?? 0}px, ${Translation?.xy?.[1] ?? 0}px)`,
          flexGrow: 1,
          minHeight: 400,
        }}
      >
        <CChart Controller={Controller} />
      </div>
    </Container>
  );
};;
