import { IconButton, SwipeableDrawer, useTheme, Box, Toolbar, Typography } from "@mui/material";
import { TreeView } from "@mui/lab";
import React from "react";
import { StyledTreeItems } from "./Layout";
import Icon from "@mdi/react";
import { mdiNpm, mdiChevronLeft, mdiChevronRight, mdiChevronDown, mdiGithub } from "@mdi/js";

export type NavMenuProps = {
  open: boolean;
  //   onOpen: () => void;
  //     onClose: () => void;
  onToggle: () => void;
  onNavigate: (to: string) => void;
};

export const NavMenu = (props: NavMenuProps) => {
  const { open, onToggle, onNavigate } = props;

  const theme = useTheme();
  const supportsTouch =
    typeof window !== "undefined" &&
    ("ontouchstart" in window ||
      ((window as any)?.documentTouch && document instanceof (window as any)?.DocumentTouch));

  return (
    <SwipeableDrawer
      open={open}
      onOpen={onToggle}
      anchor="right"
      PaperProps={{
        sx: {
          minWidth: 320,
          borderTopLeftRadius: 12,
          background: theme.palette.background.paper,
          overflow: "visible",
        },
      }}
      onClose={onToggle}
      ModalProps={{
        keepMounted: true,
      }}
    >
      {supportsTouch && (
        <Box
          sx={{
            height: 64,
            position: "absolute",
            bottom: "50%",
            transform: "translate(-20px, 0px)",
            background: theme.palette.primary.main,
            width: 20,
            zIndex: 3000,
            borderTopLeftRadius: 8,
            borderBottomLeftRadius: 8,
            border: "1px solid #333",
            visibility: "visible",
            paddingTop: "20px",
          }}
        >
          <IconButton size="small" sx={{ p: 0 }} onClick={onToggle}>
            <Icon path={open ? mdiChevronRight : mdiChevronLeft} size={"24px"} />
          </IconButton>
        </Box>
      )}
      <Toolbar sx={{ background: theme.palette.primary.main, pl: 2, color: theme.palette.primary.contrastText }}>
        <Typography variant="h4" component="div" style={{ marginRight: 8 }}>
          react-tech-chart
        </Typography>
        <IconButton
          size="small"
          onClick={() => {
            window?.open?.("https://github.com/carmnk/react-techchart", "_blank", "noopener");
          }}
        >
          <Icon path={mdiGithub} size={1.2} color={theme.palette.primary.contrastText} />
        </IconButton>
        <IconButton size="small">
          <Icon path={mdiNpm} size={1.2} color={theme.palette.primary.contrastText} />
        </IconButton>
      </Toolbar>
      <Box sx={{ pt: 2 }}>
        <TreeView
          sx={{ height: 300 }}
          defaultExpanded={["features", "examples"]}
          defaultExpandIcon={<Icon path={mdiChevronRight} size={1} color={theme.palette.primary.main} />}
          defaultCollapseIcon={<Icon path={mdiChevronDown} size={1} color={theme.palette.primary.main} />}
        >
          <StyledTreeItems
            nodeId="motivation"
            labelText="Motivation"
            TypographyProps={{ color: "primary", variant: "h5" }}
            onClick={() => {
              onNavigate("/motivation");
              onToggle();
            }}
          ></StyledTreeItems>
          <StyledTreeItems
            nodeId="features"
            labelText="Features"
            TypographyProps={{ color: "primary", variant: "h5" }}
            onClick={() => {
              onNavigate("/features");
              onToggle();
            }}
          ></StyledTreeItems>

          <StyledTreeItems
            nodeId="docs"
            labelText="Docs"
            TypographyProps={{ color: "primary", variant: "h5" }}
            infoNode={"🚧❌"}
          >
            <StyledTreeItems nodeId="docs-install" labelText="Installation" infoNode={"🚧❌"} />
            <StyledTreeItems nodeId="docs-usage" labelText="Usage" infoNode={"🚧❌"}></StyledTreeItems>
            <StyledTreeItems
              nodeId="docs-useChartState"
              labelText="useChartState API"
              infoNode={"🚧❌"}
            ></StyledTreeItems>
            <StyledTreeItems nodeId="docs-chart" labelText="Chart Component API" infoNode={"🚧❌"}></StyledTreeItems>
            <StyledTreeItems nodeId="docs-utilities" labelText="Utilities" infoNode={"🚧❌"}></StyledTreeItems>
          </StyledTreeItems>
          <StyledTreeItems nodeId="examples" labelText="Examples" TypographyProps={{ color: "primary", variant: "h5" }}>
            <StyledTreeItems
              nodeId="examples-basics"
              labelText="Basic features (builtin)"
              onClick={() => {
                onNavigate("/examples/basics");
                onToggle();
              }}
            ></StyledTreeItems>
            <StyledTreeItems
              nodeId="examples-iContainer"
              labelText="Interactive Container"
              onClick={() => {
                onNavigate("/examples/interactive-container");
                onToggle();
              }}
            ></StyledTreeItems>
            <StyledTreeItems
              nodeId="examples-multipleInstances"
              labelText="Multiple Instances"
              onClick={() => {
                onNavigate("/examples/multiple-instances");
              }}
            ></StyledTreeItems>
            <StyledTreeItems
              nodeId="examples-realtimeUpdates"
              labelText="Realtime-Chart"
              infoNode={"🚧❌"}
            ></StyledTreeItems>
            <StyledTreeItems nodeId="examples-extensions" labelText="Extendability" infoNode={"🚧❌"}></StyledTreeItems>
          </StyledTreeItems>
        </TreeView>
        <Box></Box>
      </Box>
    </SwipeableDrawer>
  );
};
