import { Container, Typography } from "@mui/material";
import React from "react";
import CChart, {
  createIRSI,
  defaultDarkTheme,
  iEMA,
  iKAMA,
  useChartState,
  Types as T,
  defaultLightTheme,
} from "react-tech-chart";
import { fbExampleData as fbExample } from "../../Site/fbDataset";

const settings: T.ChartStateProps["settings"] = {
  disableTheme: true,
  initialState: defaultDarkTheme,
  initWidthPerTick: 8,
  // containerMode: "static",
  // additionalIndicators: [iBuyStop, iSellStop],
};

const fbExampleData = fbExample.map((dat) => ({ ...dat, date: new Date(dat.date) }));

export const ExamplesMultipleInstances = (props: { darkMode: boolean }) => {
  const { darkMode } = props;
  const Controller1 = useChartState({
    data: [
      { data: fbExampleData, name: "Meta Platforms", type: "chart", id: "mainchart" },
      {
        id: "kama_01",
        type: "indicator",
        indicator: iKAMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },

      { type: "indicator", indicator: createIRSI({ period: 5 }), id: "rsi_01" },
      {
        id: "ema_rsi_01",
        type: "indicator",
        indSrcId: "rsi_01",
        indicator: iEMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },
    ],
    rtData: undefined,
    settings: settings,
  });
  const Controller2 = useChartState({
    data: [
      { data: fbExampleData, name: "Meta Platforms", type: "chart", id: "mainchart" },
      {
        id: "kama_01",
        type: "indicator",
        indicator: iKAMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },

      { type: "indicator", indicator: createIRSI({ period: 5 }), id: "rsi_01" },
      {
        id: "ema_rsi_01",
        type: "indicator",
        indSrcId: "rsi_01",
        indicator: iEMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },
    ],
    rtData: undefined,
    settings: settings,
  });
  const ContainerRef3 = React.useRef(null);
  const Controller3 = { ...Controller1, ContainerRef: ContainerRef3 };
  const ContainerRef4 = React.useRef(null);
  const Controller4 = { ...Controller2, ContainerRef: ContainerRef4 };
  const { Dispatch: Dispatch1, ChartState } = Controller1;
  const { Dispatch: Dispatch2 } = Controller2;

  React.useEffect(() => {
    if (darkMode !== ChartState.options.isDarkMode) {
      Dispatch1({ task: "setTheme", params: { theme: darkMode ? defaultDarkTheme : defaultLightTheme } });
      Dispatch2({ task: "setTheme", params: { theme: darkMode ? defaultDarkTheme : defaultLightTheme } });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [darkMode]); // force theme updates only if prop darkMode changes (triggered by Menu in Layout)

  return (
    <Container maxWidth="lg" sx={{ pt: 2, minHeight: "100%" }}>
      <Typography color="primary.main" component="div" variant="h3">
        Multiple Instances
      </Typography>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        {`A chart gallery resp. a composition of multiple charts can easily be implemented using simple css techniques like grid.
                Chart instances do not interfere with each other as long as each chart receive it's specific Controller prop.  
              `}
      </Typography>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        {` Below you can see the first and second chart with own logic while the third and fourth are using the logic from the component above (but own ContainerRefs).`}{" "}
        <br />
        {`Notice: linked charts are not reacting to pointer events.`}
      </Typography>

      <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridTemplateRows: "400px 400px" }}>
        <div>
          <CChart Controller={Controller1} />
        </div>
        <div>
          <CChart Controller={Controller2} />
        </div>
        <div>
          <CChart Controller={Controller3} />
        </div>
        <div>
          <CChart Controller={Controller4} />
        </div>
      </div>
    </Container>
  );
};
