import { mdiChevronRight, mdiChevronDown, mdiCheckCircle } from "@mdi/js";
import Icon from "@mdi/react";
import { TreeView } from "@mui/lab";
import { Container, Theme, Typography, TypographyProps } from "@mui/material";
import React from "react";
import { StyledTreeItems } from "../layout/Layout";

export const Motivation = (props: { theme: Theme }) => {
  const { theme } = props;

  return (
    <Container
      maxWidth="lg"
      sx={{ pt: 2, minHeight: "100%", position: "relative", top: 0, display: "flex", flexDirection: "column" }}
    >
      <Typography color="primary.main" component="div" variant="h3">
        Motivation
      </Typography>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        {`The project originally started as Visual Studio Code C# project (Xamarin with SkiaSharp) in 2019 as a little mind-challenge and to re-learn coding in a 'modern' programming language (I am actually industrial engineer ... and a nerd 😉). This app is still executable, see below.       
             `}
      </Typography>
      <Container maxWidth="md" sx={{ pt: 1, pb: 1 }}>
        <img src="./images/olChartSmall.jpg" alt="old xamarin app" width="100%" height="auto" />
      </Container>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        {`    
            It turned out that this choice was not right for this project. In my opinion Xamarin was not flexible enough, building a native app for this purpose seemed overrated. Accordingly, I stopped the project.  
             In the meantime I gave JavaScript another chance. Last time I worked with JavaScript was in the noughties before HTML5 when the web was a little different than today.     
             I enjoyed the new possibilities and was and still am enthusiastic about Typescript combined with React.
             `}
      </Typography>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        {`Also, I was never completely satisfied with existing charting solutions but some came very close. Especially on mobile devices I often experienced reduced functionality which was disappointing.  
            So I decided to "port" the old app to a react web-app in the mid of 2021 (at least some parts). 
            One might say that I am just too fussy. Yeah, maybe but I find the chart useful so there's at least one happy user 😉.
            What you see now is the library of my personal trading app which I am offering for public usage. 
             
             `}
      </Typography>{" "}
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        {`This library is not suitable for everyone. If you simply want to display a stockchart there are probably better solutions out there. 
        This library is designed for technical analysis - the usage of indicators and tools. Regarding this topic I recommend the books of John J. Murphy.  
             `}
      </Typography>
      <Typography color="text.primary" component="div" variant={"para1" as any}>
        {`Finally this library is currently still experimental: it is not tested via unit tests. Also development is not finished, there are still some functions I am missing... Contributors are very welcome. 
             `}
      </Typography>
      <div></div>
    </Container>
  );
};
