import React from "react";
import { Box, Container, Stack, Typography, useTheme } from "@mui/material";
import {
  CChart,
  Types as T,
  defaultDarkTheme,
  useChartState,
  iKAMA,
  iEMA,
  createIRSI,
  defaultLightTheme,
} from "react-tech-chart";
import Icon from "@mdi/react";
import { mdiChartBellCurve, mdiChartLine, mdiHamburger, mdiMenu, mdiPencilRuler, mdiTune } from "@mdi/js";
import { fbExampleData } from "../../Site/fbDataset";

const settings: T.ChartStateProps["settings"] = {
  // disablePointerEvents: true,
  disableTheme: true,
  initialState: defaultDarkTheme,
  initWidthPerTick: 8,
  // maxUpdatesPerSec: 40
  // additionalIndicators: [iBuyStop, iSellStop],
};

export type ExampleBasicsProps = {
  darkMode: boolean;
};

export const ExamplesBasics = (props: ExampleBasicsProps) => {
  const { darkMode } = props;
  const theme = useTheme();

  const [Data, setData] = React.useState<any>(fbExampleData.map((dat: any) => ({ ...dat, date: new Date(dat.date) })));
  const Controller = useChartState({
    data: [
      { data: Data, name: "Meta Platforms", type: "chart", id: "mainchart" },
      {
        id: "kama_01",
        type: "indicator",
        indicator: iKAMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },

      { type: "indicator", indicator: createIRSI({ period: 5 }), id: "rsi_01" },
      {
        id: "ema_rsi_01",
        type: "indicator",
        indSrcId: "rsi_01",
        indicator: iEMA,
        graphProps: {
          style: {
            strokeColor: ["#0693E3"],
          },
        },
      },
    ],
    rtData: undefined,
    settings,
  });
  const { ChartState, Dispatch } = Controller;

  React.useEffect(() => {
    if (darkMode !== ChartState.options.isDarkMode) {
      Dispatch({ task: "setTheme", params: { theme: darkMode ? defaultDarkTheme : defaultLightTheme } });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [darkMode]); // force theme updates only if prop darkMode changes (triggered by Menu in Layout)

  return (
    <Container
      sx={
        {
          // height: "max(100%, max-content)",
          label: "homeRoot",
          position: "relative",
          boxSizing: "border-box",
        } as any
      }
    >
      <Stack style={{ height: "100%", position: "relative", top: 0 }}>
        <div>
          <Typography variant="h3" color="primary">
            Basic Features (builtin)
          </Typography>

          <Typography
            variant={"para1" as any}
            component="div"
          >{`The chart component is designed to interact with the user allowing to modify most styles and settings on clientside out-of-the-box.
                    It comes by default with a hamburger-menu button in the bottom-left corner which opens the chart menu. 
                    The chartmenu is either a sidemenu (viewport width > 600px) or otherwise a bottom-anchored drawer,
                    which enables the user to customize most styles and settings.`}</Typography>

          <Typography variant={"para1" as any}>In the chart menu you can find 4 topics</Typography>
          <Box
            sx={{
              display: "grid",
              gridTemplateColumns: "32px max-content auto minmax(5px, max-content)",
              gap: 1,
              mt: 1,
              maxWidth: 800,
            }}
          >
            <div>
              <Icon
                path={mdiChartLine}
                size={"32px"}
                color="#fff"
                style={{
                  background: theme.palette.secondary.main,
                  border: "1px solid #666",
                  borderRadius: 5,
                  marginRight: "8px",
                }}
              ></Icon>
            </div>
            <div>
              <Typography color="text.primary">Chart</Typography>
            </div>
            <div>
              <Typography color="text.primary" component="div">
                user can fetch a new chart from device or url
              </Typography>
            </div>
            <div>
              <Typography
                color="secondary"
                variant="caption"
                component="div"
                style={{ flexGrow: 1, textAlign: "right", justifySelf: "flex-end" }}
              >
                changes expected🚧
              </Typography>
            </div>
            <div>
              <Icon
                path={mdiChartBellCurve}
                size={"32px"}
                color="#fff"
                style={{
                  background: theme.palette.secondary.main,
                  border: "1px solid #666",
                  borderRadius: 5,
                  marginRight: "8px",
                }}
              ></Icon>
            </div>
            <div>
              <Typography color="text.primary">Indicators</Typography>
            </div>
            <div>
              <Typography color="text.primary" component="div">
                user can add new indicators or edit existing ones
              </Typography>
            </div>
            <div></div>
            <div>
              <Icon
                path={mdiPencilRuler}
                size={"32px"}
                color="#fff"
                style={{
                  background: theme.palette.secondary.main,
                  border: "1px solid #666",
                  borderRadius: 5,
                  marginRight: "8px",
                }}
              ></Icon>
            </div>
            <div>
              <Typography color="text.primary">Tools</Typography>
            </div>
            <div>
              <Typography color="text.primary" component="div">
                user can add new tools or edit existing ones
              </Typography>
            </div>
            <div>
              <Typography
                color="secondary"
                variant="caption"
                component="div"
                style={{ flexGrow: 1, textAlign: "right", justifySelf: "flex-end" }}
              >
                currently only lines 🚧
              </Typography>
            </div>
            <div>
              <Icon
                path={mdiTune}
                size={"32px"}
                color="#fff"
                style={{
                  background: theme.palette.secondary.main,
                  border: "1px solid #666",
                  borderRadius: 5,
                  marginRight: "8px",
                }}
              ></Icon>
            </div>
            <div>
              <Typography color="text.primary">Settings</Typography>
            </div>
            <div>
              <Typography color="text.primary" component="div">
                user can edit general styles (eg. xaxis), charts, indicators, tools
              </Typography>
            </div>
            <div> </div>
          </Box>
        </div>
        <div style={{ display: "flex", flexDirection: "row", flexWrap: "wrap" }}>
          <Typography color="text.primary" component="span" variant={"para1" as any}>
            Just give it a try and click on the hamburger menu in the chart.
          </Typography>

          <Typography color="text.primary" component="span" variant={"para1" as any}>
            (➞{" "}
            <Icon
              path={mdiMenu}
              size={1}
              color="#fff"
              style={{
                background: theme.palette.primary.main,
                borderRadius: 5,
                // display: "inline-block",
                // marginTop: 8,
              }}
            ></Icon>{" "}
            {" )"}
          </Typography>
        </div>
        <div style={{ height: 400, paddingTop: 8 }}>
          <CChart Controller={Controller} />
        </div>
      </Stack>
    </Container>
  );
};;
