export type Price = {
  date: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
};

const makeMainChartData = (name: string, data: Price[], timeframe: string) => ({
  name,
  data: data.map((dataset) => ({ ...dataset, date: new Date(dataset.date) })),
  // RtData: undefined,
  type: "chart" as const,
  id: "mainchart-usatect-m5",
});

const makeChartData = (name: string, data: Price[], timeframe: string): T.InputData[] => [
  makeMainChartData(name, data, timeframe),
  {
    id: "kama_01",
    type: "indicator",
    indicator: iKAMA,
    graphProps: {
      style: {
        strokeColor: ["#0693E3"],
      },
    },
  },
  {
    id: "BuyStopper",
    type: "indicator",
    indicator: iBuyStopper,
    graphProps: {
      style: {
        strokeColor: ["#009688"],
      },
    },
  },
  {
    id: "SellStopper",
    type: "indicator",
    indicator: iSellStopper,
    graphProps: {
      style: {
        strokeColor: ["#f50057"],
      },
    },
  },
  { type: "indicator", indicator: createIRSI({ period: 5 }), id: "rsi_01" },
  {
    id: "ema_rsi_01",
    type: "indicator",
    indSrcId: "rsi_01",
    indicator: iEMA,
    graphProps: {
      style: {
        strokeColor: ["#0693E3"],
      },
    },
  },
  // { type: "indicator", indicator: iATR, id: "atr_01" },
];
const settings: T.ChartStateProps["settings"] = {
  // disablePointerEvents: true,
  disableTheme: true,
  initialState: defaultDarkTheme,
  initWidthPerTick: 8,
  // maxUpdatesPerSec: 40
  // additionalIndicators: [iBuyStop, iSellStop],
};

function App() {
  const [DarkMode, setDarkMode] = React.useState(true);

  const toggleDark = React.useCallback(() => {
    setDarkMode((current) => !current);
  }, [DarkMode]);

  // console.log(ChartState.options.isDarkMode);
  const theme = DarkMode ? muiDarkSiteTheme : muiLightSiteTheme;

  return (
    <div id="32" style={{ width: "100%", height: "100%" }}>
      <React.Fragment>
        {/* <div style={{ position: "relative", top: 0, height: "100%" }}>
        <CChart chartData={[{ data: Data, name: "coolChart" }]} viewMode="fullscreen" style="TODO" />
      </div> */}
        <ThemeProvider theme={theme}>
          <HashRouter>
            <Layout setDarkMode={toggleDark}>
              <Stack sx={{ height: "100%", label: "homeRoot", position: "relative", top: 0, left: 0 } as any}>
                <ConstructionFold />

                <Routes>
                  <Route path="/" element={<Home darkMode={DarkMode} />} />
                  <Route path="motivation" element={<Motivation theme={theme} />} />
                  <Route path="features" element={<Features theme={theme} />} />

                  <Route path="examples/basics" element={<ExamplesBasics darkMode={DarkMode} />} />
                  <Route
                    path="examples/interactive-container"
                    element={<ExamplesInteractiveContainer darkMode={DarkMode} />}
                  />
                  <Route
                    path="examples/multiple-instances"
                    element={<ExamplesMultipleInstances darkMode={DarkMode} />}
                  ></Route>
                  <Route path="legal" element={<Legal />} />
                </Routes>
              </Stack>
            </Layout>
          </HashRouter>
        </ThemeProvider>
      </React.Fragment>
    </div>
  );
}

export default App;
