import { mdiFilter } from "@mdi/js";
import Icon from "@mdi/react";
import {
  Container,
  Box,
  Typography,
  Link as ExtLink,
  useTheme,
  Divider,
  IconButton,
  Popover,
  List,
  ListItemButton,
} from "@mui/material";
import React from "react";
import { licenses } from "../Site/depLics";

import uniq from "lodash/uniq";

export const Legal: React.FC = (props: any) => {
  const theme = useTheme();

  const [Filter, setFilter] = React.useState<{ col: string; filterVal: string }[]>([]);
  const [FilterMenuAnchor, setFilterMenuAnchor] = React.useState<{
    col: "licenses" | "direct" | "onlyWebsite";
    target: any;
  } | null>(null);

  const licTypes = React.useMemo(() => uniq(Object.entries(licenses).map(([key, val]) => val?.licenses)), []);
  const licDirect = React.useMemo(() => uniq(Object.entries(licenses).map(([key, val]) => (val as any)?.direct)), []);
  const licOnlyWebsite = React.useMemo(
    () => uniq(Object.entries(licenses).map(([key, val]) => (val as any)?.onlyWebsite)),
    []
  );

  console.log(licDirect, licOnlyWebsite);
  return (
    <Container sx={{ p: 2, color: theme.palette.text.primary }} maxWidth="lg">
      <Typography color="primary.main" component="div" variant="h3">
        Licenses
      </Typography>
      <Typography>
        This website and the underlying "react-tech-chart" library are licensed under <u>MIT license</u>. Furthermore
        both are usind <u>additional dependencies</u> which require attribution to their creators/owners when
        distributed. This is applicable for this website, so please see section third party licenses below.
      </Typography>

      <Divider sx={{ mt: 2, mb: 2 }} />
      <Typography variant={"h6" as any} color="primary.main" component="div">
        react-tech-chart (MIT License)
      </Typography>
      <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="div">
        Copyright (c) 2021 C. Menk
      </Typography>
      <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="div">
        Permission is hereby granted, free of charge, to any person obtaining a copy of this software and associated
        documentation files (the "Software"), to deal in the Software without restriction, including without limitation
        the rights to use, copy, modify, merge, publish, distribute, sublicense, and/or sell copies of the Software, and
        to permit persons to whom the Software is furnished to do so, subject to the following conditions:
      </Typography>
      <Typography variant={"para1" as any} sx={{ fontSize: 16 }} component="div">
        The above copyright notice and this permission notice shall be included in all copies or substantial portions of
        the Software.
      </Typography>
      <Typography variant={"para1" as any} sx={{ fontSize: 16, pb: 2 }} component="div">
        THE SOFTWARE IS PROVIDED "AS IS", WITHOUT WARRANTY OF ANY KIND, EXPRESS OR IMPLIED, INCLUDING BUT NOT LIMITED TO
        THE WARRANTIES OF MERCHANTABILITY, FITNESS FOR A PARTICULAR PURPOSE AND NONINFRINGEMENT. IN NO EVENT SHALL THE
        AUTHORS OR COPYRIGHT HOLDERS BE LIABLE FOR ANY CLAIM, DAMAGES OR OTHER LIABILITY, WHETHER IN AN ACTION OF
        CONTRACT, TORT OR OTHERWISE, ARISING FROM, OUT OF OR IN CONNECTION WITH THE SOFTWARE OR THE USE OR OTHER
        DEALINGS IN THE SOFTWARE.
      </Typography>
      <Divider sx={{ mt: 2, mb: 2 }} />
      <Typography variant={"h6" as any} color="primary.main" component="div">
        Third party licenses
      </Typography>
      <Box
        sx={{
          display: "grid",
          gridTemplateColumns: "repeat(6, max-content)",
          gap: 4,
          justifyItems: "center",
          pt: 2,
        }}
      >
        <Box sx={{ justifySelf: "left" }}>
          <Typography component="span" sx={{ fontWeight: 700 }}>
            Package
          </Typography>
        </Box>
        <Box>
          <Typography component="span" sx={{ fontWeight: 700 }}>
            License
          </Typography>
          <IconButton
            size="small"
            onClick={(e: React.MouseEvent) => {
              setFilterMenuAnchor({ col: "licenses", target: e.target });
            }}
          >
            <Icon path={mdiFilter} size={1} />
          </IconButton>
          <Popover
            open={FilterMenuAnchor?.col === "licenses"}
            onClose={() => {
              setFilterMenuAnchor(null);
            }}
            anchorEl={FilterMenuAnchor?.col === "licenses" ? FilterMenuAnchor?.target : undefined}
            anchorOrigin={{
              vertical: "bottom",
              horizontal: "center",
            }}
            transformOrigin={{
              vertical: "top",
              horizontal: "center",
            }}
          >
            <List>
              <ListItemButton
                onClick={() => {
                  setFilter((current) => current.filter((filter) => filter.col !== "licenses"));
                  setFilterMenuAnchor(null);
                }}
              >
                ALL
              </ListItemButton>
              {licTypes.map((licType) => (
                <ListItemButton
                  onClick={() => {
                    setFilter((current) => {
                      const idx = current.findIndex((filter) => filter.col === "licenses");
                      if (idx >= 0)
                        return [
                          ...current.slice(0, idx),
                          { col: "licenses", filterVal: licType },
                          ...current.slice(idx + 1),
                        ];
                      else return [...current, { col: "licenses", filterVal: licType }];
                    });
                    setFilterMenuAnchor(null);
                  }}
                >
                  {licType}
                </ListItemButton>
              ))}
            </List>
          </Popover>
        </Box>
        <Box>
          <Typography component="span" sx={{ fontWeight: 700 }}>
            Direct/Indirect
          </Typography>
          <IconButton
            size="small"
            onClick={(e: React.MouseEvent) => {
              setFilterMenuAnchor({ col: "direct", target: e.target });
            }}
          >
            <Icon path={mdiFilter} size={1} />
          </IconButton>
          <Popover
            open={FilterMenuAnchor?.col === "direct"}
            onClose={() => {
              setFilterMenuAnchor(null);
            }}
            anchorEl={FilterMenuAnchor?.col === "direct" ? FilterMenuAnchor?.target : undefined}
            anchorOrigin={{
              vertical: "bottom",
              horizontal: "center",
            }}
            transformOrigin={{
              vertical: "top",
              horizontal: "center",
            }}
          >
            <List>
              <ListItemButton
                onClick={() => {
                  setFilter((current) => current.filter((filter) => filter.col !== "direct"));
                  setFilterMenuAnchor(null);
                }}
              >
                ALL
              </ListItemButton>
              {licDirect.map((direct) => (
                <ListItemButton
                  onClick={() => {
                    setFilter((current) => {
                      const idx = current.findIndex((cur) => cur.col === "direct");
                      if (idx >= 0)
                        return [
                          ...current.slice(0, idx),
                          { col: "direct", filterVal: direct },
                          ...current.slice(idx + 1),
                        ];
                      else return [...current, { col: "direct", filterVal: direct }];
                    });
                    setFilterMenuAnchor(null);
                  }}
                >
                  {direct ? "Direct" : "Indirect"}
                </ListItemButton>
              ))}
            </List>
          </Popover>
        </Box>
        <Box>
          <Typography component="span" sx={{ fontWeight: 700 }}>
            Only website
          </Typography>
          <IconButton
            size="small"
            onClick={(e: React.MouseEvent) => {
              setFilterMenuAnchor({ col: "onlyWebsite", target: e.target });
            }}
          >
            <Icon path={mdiFilter} size={1} />
          </IconButton>
          <Popover
            open={FilterMenuAnchor?.col === "onlyWebsite"}
            onClose={() => {
              setFilterMenuAnchor(null);
            }}
            anchorEl={!!FilterMenuAnchor ? FilterMenuAnchor?.target : undefined}
            anchorOrigin={{
              vertical: "bottom",
              horizontal: "center",
            }}
            transformOrigin={{
              vertical: "top",
              horizontal: "center",
            }}
          >
            <List>
              <ListItemButton
                onClick={() => {
                  setFilter((current) => current.filter((filter) => filter.col !== "onlyWebsite"));
                  setFilterMenuAnchor(null);
                }}
              >
                Lib and Website
              </ListItemButton>
              {[true].map((onlyWebsite) => (
                <ListItemButton
                  onClick={() => {
                    setFilter((current) => {
                      const idx = current.findIndex((cur) => cur.col === "onlyWebsite");
                      if (idx >= 0)
                        return [
                          ...current.slice(0, idx),
                          { col: "onlyWebsite", filterVal: onlyWebsite as any },
                          ...current.slice(idx + 1),
                        ];
                      else return [...current, { col: "onlyWebsite", filterVal: onlyWebsite as any }];
                    });
                    setFilterMenuAnchor(null);
                  }}
                >
                  {onlyWebsite ? "Only Website" : ""}
                </ListItemButton>
              ))}
            </List>
          </Popover>
        </Box>
        <Box>
          <Typography component="span" sx={{ fontWeight: 700 }}>
            Repo-Url
          </Typography>
        </Box>
        <Box>
          <Typography component="span" sx={{ fontWeight: 700 }}>
            License-Url
          </Typography>
        </Box>
        {Object.entries(licenses)
          .filter(([key, value]) =>
            Filter.length === 0
              ? true
              : Filter.map((filter) =>
                  filter.filterVal === "ALL" ? true : (value as any)[filter.col] !== filter.filterVal
                )?.includes(false)
          )
          .map(([key, value]) => {
            return (
              <React.Fragment>
                <Box sx={{ justifySelf: "left" }}>{key}</Box>
                <Box>{value?.licenses}</Box>
                <Box>{(value as any)?.direct ? "Direct" : "Indirect"}</Box>
                <Box>{"onlyWebsite" in value ? `✓` : null}</Box>
                <Box>
                  {"repository" in value ? (
                    <ExtLink
                      href={(value as any)?.repository}
                      target="_blank"
                      rel="noopener noreferrer"
                      sx={{ fontWeight: 700 }}
                    >
                      Repo
                    </ExtLink>
                  ) : null}
                </Box>
                <Box>
                  {"licenseUrl" in value ? (
                    <ExtLink
                      href={(value as any)?.licenseUrl}
                      target="_blank"
                      rel="noopener noreferrer"
                      sx={{ fontWeight: 700 }}
                    >
                      License
                    </ExtLink>
                  ) : null}
                </Box>
              </React.Fragment>
            );
          })}
      </Box>
    </Container>
  );
};
